<template>
  <el-card class="box-card">
    <el-form ref="dataForm" :model="param" :rules="rule" label-width="80px">
      <el-form-item label="id" prop="id">
        <el-input v-model="param.id" placeholder="id"></el-input>
      </el-form-item>
      <el-form-item label="tag" prop="tag">
        <el-input  type="number" v-model="param.tag" placeholder="tag"></el-input>
      </el-form-item>
      <el-form-item label="more_fuel" prop="more_fuel">
        <el-input type="number" v-model="param.fuel" placeholder="more_fuel"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">confirm</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        id: '',
        tag: '',
        fuel: '',
      },
      rule: {
        id: [{required: true, message: "please fill in id", trigger: 'blur'}],
        tag: [{required: true, message: "please fill in tag", trigger: 'blur'}],
        fuel: [{required: true, message: "please fill in more_fuel", trigger: 'blur'}],
      }
    }
  },
  methods: {
    onSubmit() {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          this.$axios.post('/drones/refuel', this.param).then(res => {
            if (res.success) {
              this.$message({
                message: 'operate successfully',
                type: 'success'
              });
              this.$router.back();
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    }
  }
}
</script>

